/**
 * @class  Bounds.Debug
 * Outlines the boundaries and angle of an entity.
 * @extends Component
 */
function BoundsDebug() {
	Component.call(this);
	this.color = Color();
}

BoundsDebug.prototype = {
	attributes: {
		color: Color.gray,
		opacity: 0.5,
		fill: false
	},

	create: function(attributes) {
		this.opacity = attributes.opacity;
		this.fill = attributes.fill;
	}
}


